<template>
<div>
    <nav-bar title='新增广告信息'></nav-bar>
    <div class="fx fx-ver second animated slideInDown">
       <div class="image-container">
            <input @change="fileChange" ref="files" name="upload" type="file" class="upload-image"  accept="image/*" />
            <div class="default-image fx fx-ac fx-ver fx-jc h-p100" v-if="showBox">
                <img  src="../../assets/images/camera.png" alt="">
                <p>上传广告图片</p>
            </div>
            <img ref="pre" v-if="showImg" :src="imageUrl" class="show-image"/>
       </div>
     <div class="content">
       <div class="title">
           <span class="sub-title">广告标题</span>
           <input class="txt" placeholder="请输入标题" v-model="txt1" maxlength="30" type="text">
           <i v-if="txt1" class="mint-toast-icon mintui mintui-field-error" @click="txt1 = ''"></i>
       </div>
        <div class="des">
          <span class="des-title">描述</span>
          <textarea placeholder="请输入描述信息" class="des-content">
          </textarea>
       </div>
       <p class="detail">广告信息:</p>
       <div class="text-content">
         <textarea placeholder="请输入正文。。。"></textarea>
       </div>
        <div class="price fx fx-ac fx-jb">
          <span>出价金额:</span>
          <input placeholder="请输入出价金额" type="text" maxlength="6" v-model="priceValue">
           <i v-if="priceValue" class="mint-toast-icon mintui mintui-field-error" @click="priceValue = ''"></i>
        </div>
        <div class="picktime fx fx-ac fx-jb">
            <div class="start fx-1">
            <mt-datetime-picker ref="picker1" v-model="pickerValue1" type="date" year-format="{value} 年" month-format="{value} 月" date-format="{value} 日"></mt-datetime-picker>
              <span>开始日期:</span>
              <span class="choose"  @click="openPicker(1)" v-if="!pickerValue1">请选择</span>
              <span class="choose"  @click="openPicker(1)" v-if="pickerValue1">{{pickerValue1|dateFormat}}</span>
            </div>
            <div class="start fx-1">
            <mt-datetime-picker ref="picker2" v-model="pickerValue2" type="date" year-format="{value} 年" month-format="{value} 月" date-format="{value} 日"></mt-datetime-picker>
              <span>结束日期: </span>
              <span @click="openPicker(2)"  class="choose" v-if="!pickerValue2">请选择</span>
              <span @click="openPicker(2)"  class="choose" v-if="pickerValue2">{{pickerValue2|dateFormat}}</span>
            </div>
        </div>
        <div class="pushTime">
            <div class="date-time">
                <mt-datetime-picker ref="picker3" v-model="pickerValue3" type="time">
                </mt-datetime-picker>
                <span>推广时间开始:</span>
                <span class="choose" @click="openPicker(3)" v-if="!pickerValue3">请选择</span>
                <span class="choose" @click="openPicker(3)" v-if="pickerValue3">{{pickerValue3}}</span>
            </div>
            <div class="date-time">
                <mt-datetime-picker ref="picker4" v-model="pickerValue4" type="time">
                </mt-datetime-picker>
                <span>推广时间结束:</span>
                <span class="choose" @click="openPicker(4)" v-if="!pickerValue4">请选择</span>
                <span class="choose" @click="openPicker(4)" v-if="pickerValue4">{{pickerValue4}}</span>
            </div>
        </div>
      <mt-button style="margin:15px 0;" type="primary" size="large">提交信息</mt-button>
     </div>
  </div>
</div>
</template>

<script>
import navBar from '@/components/common/navBar'
export default {
  data () {
    return {
      showBox: true,
      showImg: false,
      imageUrl: '',
      txt1: '',
      priceValue: '',
      pickerValue1: '',
      pickerValue2: '',
      pickerValue3: '',
      pickerValue4: ''
    }
  },
  components: {
    navBar
  },
  mounted () {
  },
  methods: {
    fileChange () {
      let fileObj = this.$refs.files
      console.log(fileObj)
      let windowURL = window.URL || window.webkitURL
      let dataURL
      this.showBox = false
      this.showImg = true
      if (fileObj && fileObj.files && fileObj.files[0]) {
        dataURL = windowURL.createObjectURL(fileObj.files[0])
        this.imageUrl = dataURL
      }
    },
    compressImageTobase64 (image, width, height, qua) {
      let quality = qua ? qua / 100 : '0.8'
	  let canvas = document.createElement('canvas')
	  let ctx = canvas.getContext('2d')
	  let w = image.naturalWidth
	  let h = image.naturalHeight
	  canvas.width = width || w
	  canvas.height = height || h
	  ctx.drawImage(image, 0, 0, w, h, 0, 0, width || w, height || h)
	  let data = canvas.toDataURL('image/jpeg', quality)
      return data
    },
    openPicker (type) {
      if (type === 1) {
        this.$refs.picker1.open()
      } else if (type === 2) {
        this.$refs.picker2.open()
      } else if (type === 3) {
        this.$refs.picker3.open()
      } else if (type === 4) {
        this.$refs.picker4.open()
      }
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../assets/css/animate.scss';
.second{
  padding-top: 40px;
}
.image-container{
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    height: 250px;
    overflow: hidden;
    background-color:#e7e7e7;
    .upload-image{
      position: absolute;
      top: 0;
      left: 0;
      z-index: 9;
      height: 100%;
      width: 100%;
      opacity:0;
      filter:alpha(opacity=0);
    }
    .default-image{
        p{
            padding: 10px;
            font-size: 14px;
            color: #aaa;
        }
        img{
            width: 50%;
        }
    }
    .show-image{
       position: absolute;
       left: 0;top: 0;
       width: 100%;
       max-height: 250px;
    }
}
.content{
    padding: 5px;
    background: #fff;
    box-sizing: border-box;
    .title{
        display: flex;
        align-items: center;
        width: 100%;
        height: 40px;
        margin: 5px 0;
        border: 1px solid #ccc;
        .sub-title{
            display: block;
            padding-left:5px;
            border-right: 1px solid #ccc;
            letter-spacing: 5px;
        }
        .txt{
            flex: 1;
            padding:0 5px;
            font-size: 14px;
            font-weight: bold;
            border: none;
            caret-color: #26a2ff;
        }
    }
  .des{
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      height: 100px;
      padding: 5px;
      border: 1px solid #ccc;
      box-sizing: border-box;
    .des-title{
        text-indent: 15px;
        border-right: 1px solid #ccc;
        letter-spacing: 20px;
    }
    .des-content{
      flex: 1;
      outline: 0;
      height: 100%;
      font-size: 14px;
      padding:5px 20px 0 5px;
      border: none;
      caret-color: #26a2ff;
    }
  }
  .detail{
     padding-left: 10px;
     margin-top: 10px;
  }
  .text-content{
      width: 100%;
      height: 150px;
      padding: 8px;
      padding-right: 20px;
      margin-top: 10px;
      border: 1px solid #ccc;
      box-sizing: border-box;
      textarea{
          width: 100%;
          height: 100%;;
          outline: 0;
          border: none;
          font-size: 14px;
          caret-color: #26a2ff;
      }
  }
  .price{
      margin-top: 10px;
      padding-right: 5px;
      border: 1px solid #ccc;
      height: 30px;
      font-size: 14px;
      span{
          padding: 0 5px;
          border-right: 1px solid #ccc;
      }
      input{
          border: none;
          outline: 0;
          flex: 1;
          padding: 0 5px;
          caret-color: #26a2ff;
      }
  }
  .picktime{
      margin-top: 10px;
      .start{
          font-size: 14px;
          color: #555;
          .choose{
              margin-left: 5px;
              color: rgb(69, 172, 37);
              font-size: 12px;
          }
      }
  }
  .pushTime{
      margin:10px 0;
      font-size: 14px;
      .choose{
        margin-left: 10px;
        color: rgb(69, 172, 37);
        font-size: 12px;
      }
  }
}
</style>



